  transition: opacity .1s;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.search-button:before {
  content: "";
  cursor: pointer;
  width: 30px;
  height: 30px;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0);
  background-image: url("pngwing.com.656ec9aa.png");
  background-size: cover;
  border: none;
  border-radius: 50%;
