html {
  scroll-behavior: smooth;
}

body {
  width: 100%;
  background-image: url('../images/bg-dark-4820.png');
  background-attachment: fixed;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  color: white;
  font-size: 12px;
  line-height: 1.86;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 16px;
  padding-right: 16px;
}
